import { Component, Host, h, Prop } from '@stencil/core';

@Component({
  tag: 'button-box',
  styleUrl: 'button-box.css',
  shadow: false,
})
export class ButtonBox {
  @Prop() buttonName: string;

  render() {
    return (
      <Host>
        <button type="button">{this.buttonName}</button>
      </Host>
    );
  }

}