import { Component, Host, h, Prop } from '@stencil/core';

@Component({
  tag: 'input-box',
  styleUrl: 'input-box.css',
  shadow: false,
})
export class InputBox {
  @Prop() fieldName: string;
  @Prop() isRequired: boolean;
  @Prop() fieldType: string;

  render() {
    return (
      <Host>
          <div class="field-wrap">
            <label class="login-username">{this.fieldName}
              {this.isRequired ?
              <span class="req">*</span>
              : null }
            :</label>
            <input name={this.fieldName} required={this.isRequired} type={this.fieldType}/>
          </div>
      </Host>
    );
  }
}
