:host {
  display: block;
}

input-box label {
  position: absolute;
  transform: translateY(6px);
  left: 13px;
  transition: all 0.25s ease;
  pointer-events: none;
  font-size: 22px;
}
input-box label .req {
  margin: 2px;
  color: green;
}
input-box label.active {
  left: 2px;
  font-size: 14px;
}
input-box label.active .req {
  opacity: 0;
}
input-box label.highlight {
  color: #fff;
}
input-box input {
  font-size: 22px;
  display: block;
  padding-left: 130px;
  padding-bottom: 8px;
  padding-top: 8px;
  border: 1px solid lightgray;
  border-radius: 0;
}
input-box input:focus {
  outline: 0;
  border-color: green;
}
input-box .field-wrap {
  position: relative;
  margin-bottom: 20px;
}